.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background: #192026;
}
.divider {
  width: 100%;
  fill: #381257;
}
.segment {
  background: linear-gradient(#f8f8f8, #f5f5f5);
  background: linear-gradient(#bdbdbd, #f5f5f5);
  background-color: #FFFFFF; /* Defina a cor de fundo desejada aqui */
}